import React, { useCallback, useState } from 'react';

// styles
import {
  Container,
  Input,
  InputLabel,
  InputWrapper,
  Wrapper,
  Select,
  Button,
  InputIcon,
} from './styles';
import Modal from '../Modal';

const Calculator = () => {
  const [currentOption, setCurrentOption] = useState(0.7);
  const [companyFee, setCompanyFee] = useState(0);
  const [inputValue, setInputValue] = useState('');

  const [errorMessage, setErrorMessage] = useState('');

  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleOpenLink = useCallback(() => {
    const url =
      'https://api.whatsapp.com/send?phone=5532991215205&text=Olá!%20Quero%20restituir%20os%20impostos%20da%20minha%20empresa.';
    window.open(url, '_blank').focus();
  }, []);

  const handleOpenModal = useCallback(() => {
    if (companyFee > 400000) {
      setErrorMessage(
        'Infelizmente sua empresa não se encaixa no Simples Nacional.\nEntre em contato conosco para conversar sobre a sua contabilidade.',
      );
    } else if (companyFee <= 0) {
      setErrorMessage('Por favor, informe um faturamento mensal válido!');
    }

    setIsModalOpen(true);
  }, [companyFee]);

  const handleCloseModal = useCallback(() => {
    setIsModalOpen(false);
    setErrorMessage('');
  }, []);

  const handleSelectOption = useCallback(option => {
    setCurrentOption(option);
  }, []);

  const handleChangeInput = useCallback(value => {
    const onlyDigits = value
      .split('')
      .filter(s => /\d/.test(s))
      .join('')
      .padStart(3, '0');

    const monthlyAmount = parseFloat(onlyDigits) / 100;
    setCompanyFee(monthlyAmount);

    const digitsFloat = onlyDigits.slice(0, -2) + '.' + onlyDigits.slice(-2);
    const valueFormatted = new Intl.NumberFormat('pt-BR', {
      minimumFractionDigits: 2,
      maximumFractionDigits: 2,
    }).format(digitsFloat);

    setInputValue(valueFormatted);
  }, []);

  return (
    <Container>
      <Wrapper>
        <InputLabel>INFORME SEU FATURAMENTO MENSAL</InputLabel>
        <InputWrapper>
          <InputIcon>R$</InputIcon>
          <Input
            value={inputValue}
            onChange={e => handleChangeInput(e.target.value)}
            type="text"
            placeholder="20.000,00"
          />
        </InputWrapper>
      </Wrapper>
      <Wrapper>
        <InputLabel>QUAL É O SEU SEGMENTO?</InputLabel>
        <Select onChange={e => handleSelectOption(e.target.value)}>
          <option value="0.70">Adega</option>
          <option value="0.80">Autopeças</option>
          <option value="0.25">Bares/Restaurantes</option>
          <option value="0.25">Casa de show</option>
          <option value="0.50">Centro automotivo</option>
          <option value="0.90">Farmacias</option>
          <option value="0.70">Loja de conveniencia</option>
          <option value="0.10">Mercados/Mercearias</option>
          <option value="0.85">Perfumaria</option>
          <option value="0.30">Petshop</option>
        </Select>
      </Wrapper>
      {!isModalOpen && (
        <Wrapper>
          <Button onClick={handleOpenModal}>CALCULAR SEU CRÉDITO!</Button>
          <Button onClick={handleOpenLink}>QUERO RESTITUIR AGORA!</Button>
        </Wrapper>
      )}
      <Modal
        isOpen={isModalOpen}
        companyFee={companyFee}
        percentage={currentOption}
        errorMessage={errorMessage}
        handleCloseModal={handleCloseModal}
        handleOpenLink={handleOpenLink}
      />
    </Container>
  );
};

export default Calculator;
